import { DeepPartial } from '../core'
import { Operator } from './operator.model'

/**
 * Operator MOCK
 */
export function OperatorMock(options?: DeepPartial<Operator>): Operator {
  const now = new Date().toISOString()

  return {
    _id: options?._id || 'OPERATOR_ID',

    name: options?.name || 'OPERATOR_NAME',
    code: options?.code || 'OPERATOR_CODE',
    type: options?.type || 'PROTOCOL',

    rating: options?.rating,
    location: options?.location,

    createdAt: options?.createdAt || now,
    createdBy: options?.createdBy || 'USER_ID',
    updatedAt: options?.updatedAt || now,
    updatedBy: options?.updatedBy || 'USER_ID',
  }
}
